import React from 'react'
import HomeIcon from '@material-ui/icons/Home';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import SubscriptionsIcon from '@material-ui/icons/Subscriptions';
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary';
import HistoryIcon from '@material-ui/icons/History';
import WatchLaterIcon from '@material-ui/icons/WatchLater';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import Items from "./Items";
import { Link } from "react-router-dom";
import './sidebar.css';
function sidebar() {
    return (
        <div className='items'>
            <Link to={'/rec'}>
                <Items Icon={HomeIcon} name='Home' className='items_item' />
                <Items Icon={WhatshotIcon} name='Trending' className='items_item' />
                <Items Icon={SubscriptionsIcon} name='Subscribe' className='items_item' />
                <hr className='hr' />
                <Items Icon={VideoLibraryIcon} name='Library' className='items_item' />
                <Items Icon={HistoryIcon} name='History' className='items_item' />
                <Items Icon={WatchLaterIcon} name='Watch later' className='items_item' />
                <Items Icon={ThumbUpAltIcon} name='Liked videos' className='items_item' />
                <Items Icon={ArrowDropDownIcon} name='Show more' className='items_item' />
            </Link>
        </div>
    )
}

export default sidebar
