import React from 'react'
import Avatar from '@material-ui/core/Avatar';
import './videocard.css'

function videocard({ title, views, rel, img, ch }) {
    return (
        <div className='card'>
            <a href={img} target="_blank"><img src={img} className='img' /></a>
            <div className="container">
                <Avatar alt="Channel" src={ch} className='avt' />
                <div className="info">
                    <div className="title" className='title'>{title}</div>
                    <div className="vr">
                        <div>{views}</div>.
                        <div>{rel}</div>                     
                    </div>
                </div>
            </div>
        </div>
    )
}

export default videocard
