import React, { useState } from 'react'
import "./header.css";
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import VideoCallIcon from '@material-ui/icons/VideoCall';
import AppsIcon from '@material-ui/icons/Apps';
import MicIcon from '@material-ui/icons/Mic';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Avatar from '@material-ui/core/Avatar';
import { Link } from "react-router-dom";

function Header() {

    const [str, setStr] = useState('');
    return (
        <div className='header'>

            <div className='header_left'>
                <MenuIcon className='header_leftt' />
                <Link to={'/'}>
                    <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/b/b8/YouTube_Logo_2017.svg/110px-YouTube_Logo_2017.svg.png" alt="" srcset="" className='header_leftt' />
                </Link>
            </div>

            <div className='header_center'>
                <input value={str} onChange={(e) => setStr(e.target.value)} type="text" placeholder='Search' />
                <Link to={`/search/${str}`}>
                    <SearchIcon type='submit' className='header_centerr' />
                </Link>
                <MicIcon className='header_centerr' />
            </div>

            <div className='header_right'>
                <VideoCallIcon className='header_rightt' />
                <AppsIcon className='header_rightt' />
                <NotificationsIcon className='header_rightt' />
                <Avatar alt="Remy Sharp" src="/static/images/avatar/1.jpg" className='header_rightt' />
            </div>

        </div>
    )
}

export default Header
